.about {
  background-color: $even-section-bgc;
  cursor: url(../images/cursor1.png), default;
}

.container--mobile {
  @include mq(mobile-only) {
    background-image: url(../images/bgi-mb.png);
    background-image: -webkit-image-set(url(../images/bgi-mb.webp) 1x);
    background-repeat: no-repeat;
    background-position: center;
    background-position-y: 75%;
    background-size: contain;

    @include mq(retina) {
      background-image: url(../images/bgi-mb@2x.png);
      background-image: -webkit-image-set(url(../images/bgi-mb@2x.webp) 2x);
    }
  }
}

// // Desktop background
.about__background {
  @include mq(desktop) {
    background-image: url(../images/bgi-ds.png);
    background-image: -webkit-image-set(url(../images/bgi-ds.webp) 1x);
    background-repeat: no-repeat;
    background-position: center;
    background-position-y: 80%;
    background-size: 1278px;

    @include mq(retina) {
      background-image: url(../images/bgi-ds@2x.png);
      background-image: -webkit-image-set(url(../images/bgi-ds@2x.webp) 2x);
    }
  }
}

.container--mobile {
  @include mq(mobile-only) {
    padding-right: 80px;
  }
}

.about__label {
  font-family: 'Titan One';
  @include fonts(18px, 1.17, 0);
  text-align: center;
  text-transform: uppercase;
  color: $upper-title-color;

  @include mq(tablet) {
    @include fonts(26px, 1.15, 0);
  }

  @include mq(desktop) {
    @include fonts(30px, 1.13);
  }
}

.about__title {
  margin-top: 15px;
  margin-bottom: 32px;

  font-family: 'Titan One';
  @include fonts(34px, 1.14);
  text-align: center;
  text-transform: uppercase;
  color: $accent-title-color;

  @include mq(tablet) {
    @include fonts(48px, 1.15, 0);
  }
  @include mq(desktop) {
    @include fonts(58px, 1.14, 0.06em);
  }
}

.about__image {
  @include mq(tablet) {
    width: 336px;
    height: 335px;
  }

  @include mq(desktop) {
    width: 501px;
    height: 501px;
  }
}

.about__description-first-part {
  margin-bottom: 40px;
  @include fonts(12px, 1.58, 0);
  font-weight: 500;
  color: $main-subtitle-color;
  text-align: left;

  @include mq(tablet) {
    max-width: 334px;
    @include fonts(14px, 1.78, 0);
  }

  @include mq(desktop) {
    margin-bottom: 32px;
    max-width: 463px;
    @include fonts(16px);
  }
}

.about__description-second-part,
.about__description-third-part {
  @include fonts(12px, 1.58, 0);
  font-weight: 500;
  color: $secondary-text-color;
  text-align: left;

  @include mq(tablet) {
    max-width: 334px;
    @include fonts(14px, 1.78, 0);
  }

  @include mq(desktop) {
    @include fonts(14px, 1.92);
    font-size: 14px;
    max-width: 463px;
  }
}

.about__description-second-part {
  margin-bottom: 14px;
}

.about__description-third-part {
  margin-bottom: 20px;

  @include mq(tablet) {
    margin-bottom: 40px;
  }
}

.about__button {
  display: flex;
  align-items: center;
  justify-content: center;
  padding-top: 13px;
  padding-bottom: 13px;
  width: 176px;
  border-radius: 22px;
  outline: transparent;
  border: transparent;
  @include fonts(14px, 1.28, 0);
  font-weight: 700;
  text-align: center;
  color: $accent-title-color;
  background: $ligth-mode-btn-bgc;
  background-color: $hero-uptitle-color;
  transition: $transition-color-bgc;
  cursor: url(../images/cursor-active1.png), default;
  @include mq(tablet) {
    @include fonts(16px, 1.31);
  }

  &:hover,
  &:focus {
    background-color: $ligth-mode-btn-bgc-hover;
  }
}

.about__button-icon {
  margin-left: 14px;
  fill: currentColor;
}

.about-wrapper {
  @include mq(tablet) {
    display: flex;
  }
}

.about-txt-wrapper {
  @include mq(tablet) {
    margin-left: 32px;
  }
  @include mq(desktop) {
    margin-left: 139px;
    padding-top: 72px;
  }
}
